.card {  
    font-family: $font-family-primary;
    color: #151C22;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    height: 100%;  

    &__img {      
        margin-bottom: 25px;      

        img {     
            @media (max-width: 1024px) {
                width: 324px;
            }
        }
    }

    &__heading {
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 13px;
        margin: auto 0 15px 0;   


        @media (max-width: 1024px) {
            margin-top: 0;
        }
    }

    &__price {
        margin: 0;
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 11px;
    }
}